<template>
  <div id="deleteUser" style="display: inline-block;">
    <v-icon title="删除用户" @click.stop="showDeleteUserDialog()" v-text="icon" />
    <v-dialog v-model="deleteUserDialog" persistent max-width="300px">
      <v-card>
        <v-card-title>
          确定删除该用户吗?
          <v-spacer />
          <v-icon aria-label="Close" @click="deleteUserDialog = false">mdi-close</v-icon>
        </v-card-title>

        <v-card-text class="pb-6 pt-12 text-center">
          <v-btn class="mr-3" text @click.stop="deleteUserDialog = false">取消</v-btn>
          <v-btn color="success" text @click.stop="deleteUser">确定</v-btn>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  data: () => ({
    icon: "mdi-delete",
    deleteUserDialog: false,
  }),

  props: {
    item: {
      type: Object
    },
    selected: {
      type: Array
    }
  },

  computed: {
    
  },

  watch: {
    
  },

  created() {
  },

  methods: {
    showDeleteUserDialog() {
      if(!this.item && !(this.selected && this.selected.length > 0)) {
         this.$store.commit("SET_SNACK", {
            success: false,
            msg: "请选择用户"
          });

          return;
      }

      this.deleteUserDialog = true;
    },

    deleteUser() {
      let ids = [];
      if(!!this.item) {
        ids.push(this.item.id);
      } 
      
      if(!!this.selected && this.selected.length > 0) {
         this.selected.forEach(function(
            selection,
            index
          ) {
            ids.push(selection.id);
          });
      }

      if(ids.length ==0 ){
        this.$store.commit("SET_SNACK", {
          success: false,
          msg: "未选择用户"
        });

        this.deleteUserDialog = false;
        return;
      }

      let params = {
        ids: ids
      };
      this.$http
        .post("/user/api/v1/user/delete", params)
        .then(res => {
          if (res.success) {
            this.$emit("loadUsers");
            res.msg = "用户删除成功";
          }
          this.$store.commit("SET_SNACK", res);
        })
        .catch(error => {
          this.$store.commit("SET_SNACK", {
            success: false,
            msg: "访问异常：用户未删除"
          });
        });

      this.deleteUserDialog = false;
    },
  },
};
</script>

